<template>
  <div class="contact">
    <header>
      <div class="search">
        <input v-model="value" type="text">
        <span @click='search_Tap'>搜索关键词</span>
      </div>
      <nav>
        <ul>
          <li class="fir" @click='affir_Tap'>申明</li>
          <li @click='nav_Tap(index)' v-for='(item,index) in nav_list' :key='index' :class="index==type?'active':''">
            {{item}}
          </li>
        </ul>
      </nav>
    </header>
    <main>
      <ul class="list">
        <li v-for="(item, index) in list_datas" :key="index">
          <div @click='item_Tap(item.id)'>
            <h2>
              <span>{{index+1}}问：</span>
              {{item.question}}
            </h2>
            <img :src="active_p==index+1?active_img:hide_img" alt="">
          </div>
          <p v-show="active_p==index+1">{{item.answer}}</p>
        </li>
      </ul>
    </main>
    <div class="shadow" v-if="isshow">
      <div>
        <div class="head">
          <h1>申明</h1>
          <img src="../../assets/home_img/x.png" alt="" @click='hide_Tap'>
        </div>
        <p class="cont">{{cont}}</p>
        <p class="btn" @click='hide_Tap'>确定</p>
      </div>
    </div>
  </div>
</template>

<script>
  import request from "@/request.js";
  import axios from "axios"
  export default {
    data() {
      return {
        active_p: '',
        active_img: require('../../assets/home_img/shang.png'),
        hide_img: require('../../assets/home_img/xia.png'),
        value:'',
        type: 0,
        nav_list: ['全部', '装修前', '装修中', '装修后'],
        isshow: false,
        list_datas: [],
        cont: '这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明这是问答申明',

      }
    },
    watch: {
      value:function(){
        console.log(this.value);
        
      }
    },
    created() {
      this.$store.commit('show_looding')
      request({
        url: '/api/getQuestionsAnswersList',
        method: "post",
      }).then(data => {
        if (data.status == 200) {
          this.list_datas = data.data.data
        }
        this.$store.commit('hide_looding')
      })
      
    },
    methods: {
      search_Tap() {
        if (this.value == '') {
          this.$dialog.alert({
            message: '请输入搜索的内容',
            confirmButtonColor: '#4A8AFD'
          })
        } else {
          var a
          if(this.value==''){
             a=''
          }
          this.$store.commit('show_looding')
          request({
            url: '/api/getQuestionsAnswersList',
            params:{ type: this.type, search:this.value },
            method: "post",
          }).then(data => {
            console.log(data)
            if (data.status == 200) {
              this.list_datas = data.data.data
            }
            this.$store.commit('hide_looding')
          })
          
        }
      },
      nav_Tap(i) {
        this.type = i
        request({
          url: '/api/getQuestionsAnswersList',
          params: { type: this.type, search: this.value },
          method: "post",
        }).then(data => {
          console.log(data)
          if (data.status == 200) {
            this.list_datas = data.data.data
          }
        })
      },
      affir_Tap() {
        this.isshow = true
      },
      hide_Tap() {
        this.isshow = false
      },
      item_Tap(index) {
        if (this.active_p == index) {
          this.active_p = ''
        } else {
          this.active_p = index
        }
      }
    },
  }
</script>

<style scoped>
  .contact {
    background-color: #F9E7EF;
  }

  .contact header {
    padding: 0.8rem 0.8rem 0.4rem;
  }

  .search {
    display: flex;
    border: 2px solid #FD2692;
    border-radius: 0.3rem;
    margin-bottom: 0.6rem;
  }

  .search input {
    padding: 0.6rem;
    flex: 2;
    font-size: 0.8rem;
    color: #323232;
    outline: none;
    border: none;
  }

  .search span {
    line-height: 2.2rem;
    font-size: 0.9rem;
    text-align: center;
    flex: 1;
    color: white;
    background: #FD2692;
  }

  .contact nav ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
  }

  nav ul li {
    font-size: 0.9rem;
    background-color: white;
    padding: 0.3rem 0.8rem;
    border: 2px solid #FD2692;
    border-radius: 0.3rem;
  }

  .fir {
    background: #FD2692;
    color: white;
  }

  .active {
    background-color: #2BBF4A;
    color: white;
    border: 2px solid #2BBF4A;
  }

  main .shadow {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: #00000054;
  }

  .shadow>div {
    position: relative;
    margin: 2.5rem;
    background: white;
    height: 80%;
    display: flex;
    text-align: center;
    flex-direction: column;
  }

  .shadow .head {
    height: 2.5rem;
    line-height: 2.5rem;
    color: white;
    background-color: #FD2692;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .shadow .head h1 {
    font-size: 1rem;
    font-weight: normal;
  }

  .shadow .head img {
    position: absolute;
    right: 0.8rem;
    width: 1.5rem;
    height: 1.5rem;
  }

  .shadow .cont {
    margin: 2.5rem 0 2.5rem;
    text-align: justify;
    overflow-y: auto;
    padding: 0.5rem;
    line-height: 1.5rem;
    font-size: 0.9rem;
  }

  .shadow .btn {
    position: absolute;
    background-color: white;
    left: 0;
    bottom: 0;
    right: 0;
    height: 2.3rem;
    border-top: 1px solid #ddd;
    line-height: 2.3rem;
    font-size: 1rem;
    color: #FD2692;
  }

  .list li {
    margin-bottom: 0.6rem;
    box-shadow: 0 1px 2px rgba(0, 0, 0, .3);
  }

  .list li>div {
    border-bottom: 1px solid #d9d9d9;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0.8rem;
    background: white;
  }

  .list li h2 {
    font-size: 0.9rem;
    font-weight: normal;
  }

  .list li h2 span {
    color: #FD2692;
  }

  .list li>div img {
    width: 0.8rem;
    height: 0.8rem;
    margin-left: 0.4rem;
  }

  .list li p {
    font-size: 0.9rem;
    font-weight: 500;
    background: white;
    padding: 0.8rem;
  }
</style>


